.nav {
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 11vh;
  width: 100%;
  padding-right: 20px;
  padding-left: 60px;
  z-index: 100;
  transition: 0.3s all ease-in-out;
  overflow: hidden;
  font-family: museo-sans-rounded, sans-serif;
  font-style: normal;
}

.nav.nav-solid {
  height: 9vh;
  color: white;
  background: rgb(34, 34, 34);
  box-shadow: 0 0 10px rgba(36, 36, 36, 0.486);
  transition: 0.3s all ease-in-out;
  overflow: hidden;
}

.nav .logo {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 100%;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.nav .logo img {
  /* padding: 0;
  margin: 0;
  transform: translate(-15%, 10%);
  cursor: pointer; */
  width: 150px;
  padding-top: 15px;
  object-fit: cover;
  cursor: pointer;
}

.nav ul {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
  -webkit-flex: 2.5 1;
          flex: 2.5 1;
}

.nav ul li {
  position: relative;
  font-size: 20px;
  font-weight: 900;
}

.nav ul li:before {
  content: "";
  position: absolute;
  bottom: -9%;
  height: 4px;
  border-radius: 3px;
  width: 0%;
  background: #fdeea5;
  transition: 0.3s all ease-in-out;
}

.nav ul li:hover::before {
  width: 18px;
}

.nav .btn {
  -webkit-flex: 1.4 1;
          flex: 1.4 1;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.nav .btn img {
  width: 150px;
  object-fit: contain;
}

.nav .btn button {
  background: var(--background-btn);
  border: none;
  outline: none;
  padding: 8px 20px;
  width: 150px;
  color: white;
  font-size: 19px;
  font-weight: 700;
  border-radius: 3px;
  font-family: museo-sans-rounded, sans-serif;

  cursor: pointer;
}

.nav .toggle {
  display: none;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.nav .toggle img {
  cursor: pointer;
}

.nav .display {
  display: none;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.nav .display img {
  width: 40px;
  height: 40px;
  object-fit: cover;
  cursor: pointer;
  border-radius: 50%;
}

@media screen and (max-width: 970px) {
  .nav {
    padding: 0 40px;
  }

  /* .nav .logo img {
    transform: translate(-24%, 10%);
  } */
}

@media screen and (max-width: 860px) {
  .nav ul {
    display: none;
  }

  .nav .toggle {
    display: -webkit-flex;
    display: flex;
  }

  .nav .display {
    display: -webkit-flex;
    display: flex;
  }

  .nav .btn {
    display: none;
  }

  .nav .logo {
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
  }

  .nav .logo .empty {
    display: none;
  }

  .nav .display {
    -webkit-flex: 0.5 1;
            flex: 0.5 1;
  }

  .nav .toggle {
    -webkit-flex: 0.5 1;
            flex: 0.5 1;
  }

  .nav .toggle img {
    width: 40px;
  }

  /* .nav .logo img {
    transform: translate(-24%, 10%);
  } */

  .nav {
    padding: 0 60px;
  }
}

@media screen and (max-width: 500px) {
  .nav {
    padding: 0 30px;
  }

  .nav .toggle img {
    width: 30px;
  }
}

@media screen and (max-width: 360px) {
  .nav {
    padding: 0 15px;
  }
}

